<template>
  <div>
    <el-form size="mini" :model="form" inline>
      <el-form-item label="商品名称">
        <el-input v-model="form.prodName" />
      </el-form-item>
      <el-form-item label="成交时间">
        <el-date-picker v-model="form.startTime" type="date" placeholder="选择日期" style="width: 205px"></el-date-picker> 至 <el-date-picker v-model="form.endTime" type="date" placeholder="选择日期" style="width: 205px"></el-date-picker>
      </el-form-item>
    </el-form>
    <el-form size="mini" :model="form" inline>
      <el-form-item label="买家昵称">
        <el-input v-model="form.name" />
      </el-form-item>
      <el-form-item label="订单编号">
        <el-input v-model="form.orderCode" />
      </el-form-item>
      <el-form-item>
        <el-button class="inquire1" type="success" style="width: 70px" @click="onSearch">查询</el-button>
        <el-button style="width: 70px" @click="onReset">重置</el-button>
      </el-form-item>
    </el-form>
    <div class="nav_group">
      <div class="nav_text" :class="navIndex == 0 ? 'action' : ''" @click="onNavIndex(0)">近三个月订单</div>
      <div class="nav_text" :class="navIndex == 1 ? 'action' : ''" @click="onNavIndex(1)">已付款</div>
      <div class="nav_text" :class="navIndex == 2 ? 'action' : ''" @click="onNavIndex(2)">已发货</div>
      <div class="nav_text" :class="navIndex == 3 ? 'action' : ''" @click="onNavIndex(3)">已签收</div>
      <div class="nav_text" :class="navIndex == 4 ? 'action' : ''" @click="onNavIndex(4)">退款售后</div>
    </div>
    <el-table style="width: 100%" :data="payOrderList" row-key="id" :load="load" :tree-props="{ children: 'children', hasChildren: 'hasChildren' }" lazy border size="mini">
      <el-table-column prop="orderCode" label="订单号" align="center"></el-table-column>
      <el-table-column prop="prodName" label="商品" width="280" align="center">
        <template slot-scope="scope">
          <div class="goods_box">
            <el-image :src="scope.row.prodImageUrl" style="width: 50px; height: 50px" fit="cover"></el-image>
            <span class="goods_name">{{ scope.row.prodName }}</span>
          </div>
        </template>
      </el-table-column>
      <el-table-column prop="price" label="单价" align="center"></el-table-column>
      <el-table-column prop="amount" label="数量" align="center"></el-table-column>
      <el-table-column prop="count" label="明细数量" align="center"></el-table-column>
      <el-table-column prop="createTime" label="日期" align="center"></el-table-column>
      <el-table-column prop="name" label="买家昵称" align="center"></el-table-column>
      <el-table-column prop="userName" label="买家" align="center"></el-table-column>
      <el-table-column prop="orderStatus" label="交易状态" align="center">
        <template slot-scope="scope">
          <span v-if="scope.row.orderStatus === 0" style="color: #40ccae">待付款</span>
          <span v-else-if="scope.row.orderStatus === 1" style="color: #e39025">待发货</span>
          <span v-else-if="scope.row.orderStatus === 2" style="color: #49ca26">已发货</span>
          <span v-else-if="scope.row.orderStatus === 3" style="color: #c5fd68">待评价</span>
          <span v-else-if="scope.row.orderStatus === 4" style="color: #e02929">退款中</span>
          <span v-else-if="scope.row.orderStatus === 10" style="color: #666666">已完成</span>
          <span v-else-if="scope.row.orderStatus === 9" style="color: #999999">已关闭</span>
        </template>
      </el-table-column>
      <el-table-column prop="payAmount" label="实收货款" align="center"></el-table-column>
    </el-table>
    <!-- 分页 -->
    <el-pagination background layout="prev, pager, next" :total="total" @current-change="handleCurrentChange"></el-pagination>
  </div>
</template>

<script>
export default {
  name: 'PayOrderList',

  data() {
    return {
      navIndex: 0, // 点下面的导航栏
      pageNo: 1, // 当前分页
      startTime: '', // 近三个月的订单
      payOrderList: [], // 订单列表
      total: 0, // 分页总条数
      form: {}, // 搜索用的表单
    }
  },

  methods: {
    load(tree, treeNode, resolve) {
      this.itemfindByOrderId(tree.id)
        .then((res) => {
          if (res.data.success) {
            let orderData = {
              userName: tree.userName,
              orderStatus: tree.orderStatus
            }
            let itemOrderData = res.data.data.map((item) => {
              return { ...item, ...orderData }
            })
            resolve(itemOrderData)
          } else {
            this.$message.error(res.data.msg)
          }
        })
    },
    onNavIndex (navIndex) {
      this.navIndex = navIndex
      this.apiOrderList()
    },
    // 分页
    handleCurrentChange (pageNo) {
      this.pageNo = pageNo
      this.apiOrderList()
    },
    // 搜索
    onSearch () {
      this.apiOrderList()
    },
    // 重置
    onReset () {
      this.form = {}
    },

    // api-----------------------------------------------
    // 订单分页查询
    apiOrderList () {
      switch (this.navIndex) {
        case (0): {
          console.log('this.startTime', this.form.startTime)
          this.startTime = this.form.startTime || new Date(new Date().getTime() - 24 * 60 * 60 * 1000 * 30 * 3)
          break
        }
        case (1): {
          this.startTime = ''
          this.orderStatus = 1
          break
        }
        case (2): {
          this.startTime = ''
          this.orderStatus = 2
          break
        }
        case (3): {
          this.startTime = ''
          this.orderStatus = 3
          break
        }
        case (4): {
          break
        }
      }
      let data = {
        pageNo: this.pageNo,
        ...this.form
      }
      // 模糊查询
      data.startTime = this.startTime
      this.$http.post('/woho/order/head/query', data)
        .then((res) => {
          if (res.data.success) {
            this.payOrderList = res.data.data.list
            this.total = res.data.data.total
            this.payOrderList.map((item) => {
              item.hasChildren = true
            })
          } else {
            this.$message.error(res.data.msg)
          }
        })
    },
    // 根据id查询订单明细
    itemfindByOrderId (id) {
      return this.$http.post(`/woho/order/item/findByOrderId/${id}`).then((res) => res)
    }
  },

  created () {
    this.apiOrderList()
  }
}
</script>

<style lang="scss" scoped>
  $themColor: #5bd9be;

  .nav_group{
    display: flex;
    align-items: center;
    font-size: 14px;
    .nav_text{
      padding: 10px 20px;
      color: #000;
      cursor: pointer;
    }
    .action{
      color: $themColor;
      font-weight: bold;
      border-bottom: 2px solid $themColor;
    }
  }
  .goods_box {
    display: flex;
    justify-content: space-between;
    align-items: center;
    .goods_name{
      width: 200px;
      overflow: hidden;
    }
  }
</style>
